@import url(//netdna.bootstrapcdn.com/font-awesome/3.0/css/font-awesome.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --highlight-pink:#DB4272;
  --background-pink: #E69FA2;
  --highlight-blue: #84D4E3;
  --highlight-yellow: #F7C83E;
  --background-green: #4D5B27;
  --background-tan: #DCD9A1;
  background-color: white;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}

body {
  overflow: overlay;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  color: #4D5B27;
  color: var(--background-green);
}
.title {
  font-family: 'Anton', sans-serif;
  color: #F7C83E;
  color: var(--highlight-yellow);
  font-size: 2rem;
  padding-left: 1%;
  letter-spacing: 1.75px;
}
.header {
  background-color: #4D5B27;
  background-color: var(--background-green);
  display: flex;
  align-items: center;
  justify-content: left;
  height: 7.5vh;
  width: 100vw;
  margin: 0;
  padding: 0;
}




.mainContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

a {
  text-decoration: none;
  color: #4D5B27;
  color: var(--background-green)
}
.newButton {
  border-radius: 100%;
  background-color: transparent;
  border-style: solid;
  font-weight: bolder;
  
}

.newButton:hover {
  color: #DB4272;
  color: var(--highlight-pink);
  border-color: #DB4272;
  border-color: var(--highlight-pink);
}




a, .newButton {
  margin-left: 2%;
}

.myBeerListContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}
ul {
  list-style: none;
}

.bottom {
  margin-bottom: 5%;
  cursor: pointer;
}

.beerIcon {
  width: 30px;
  height: 30px;
  margin-left: 5%;
  margin-right: 0;
}


.beerItemContainer {
    border: 4px solid var(--background-green);
    border-radius: 25px;
    margin: 2%;
    padding: 5%;
    max-width: 100%;
    min-width: 100%;
    font-weight: bold;
}

.beerItemContainer:hover {
    background-color: var(--background-tan);
}

.name {
    font-size: larger;
    margin: 0;
}




.beerListContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2%;
}
.searchContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5% 0% 5% 0%;
    width: 300px;
}
.subSearch {
    display: flex;
    justify-content: space-evenly;
    
}
.searchItem {
    margin: 1%;
}

.textSearch {
    width: 100%;
    font-size: 1.05rem;
    font-family: FontAwesome;
}

.beerFormContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 50vh;
    width: 50vw;
}

.formInputText, .formInputSelect {
    margin: 0.4%;
    border-left: 2px solid lightgray;
    border-top: none;
    border-right: none;
    border-bottom: 2px solid lightgray;
    border-radius: .15em;
    width: 95%;
}
.beerPageContainer {
    color: var(--background-green);
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10%;
    font-size: 1.15rem;
    width: 100%;
    height: 75%;
}

.beerButtonContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.beerTextTitle {
    font-size: 2rem;
}
